import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Campaign from "../components/campaign"
import "../styles/main-home.css"

export default function Home() {
  return (
    <Layout>
      <Seo title="ホーム"/>
      <div className="wrap">

        <section className="mv_hm"> 
          <div className="mv_hm-inner">
            <p className="txt1">
              DX時代の悩める小規模事業者をお助けします！<br/>
              IT専門家×経営のプロ（中小企業診断士）が、<br/>
              あなたの会社のデジタル化＆IT活用を手厚くサポート。<br/>
              稼ぐ力を高め、新時代の成長に貢献します。
            </p>
            <p className="txt2">
              <span>小さな会社のためのデジタル戦略室</span><br/>
              株式会社ホットソウル
            </p>
          </div>
        </section>

        <section className="news">
          <div className="news-inner">
            <dl className="clearfix">
              <dt>2022.2.9</dt>
              <dd><Link to={`/column/about-cookies-2`}>代表のコラム「クッキーについて（２）」</Link>を公開</dd>
              <dt>2022.2.7</dt>
              <dd><Link to={`/dx-council/tech-yokohama-2022`}>やさしいDX推進協議会の活動報告「テクヨコ2022見学レポート」</Link>を公開</dd>
              <dt>2022.1.25</dt>
              <dd><Link to={`/column/about-cookies`}>代表のコラム「クッキーについて」</Link>を公開</dd>
              <dt>2022.1.20</dt>
              <dd><Link to={`/column/management-and-haccp-system`}>中小企業診断士・高木 俊明さんのコラム「経営とHACCPシステム」</Link>を公開</dd>
              <dt>2022.1.12</dt>
              <dd className="last"><Link to={`/column/inventory-of-it-assets`}>代表のコラム「IT資産の棚卸し」</Link>を公開</dd>
            </dl>
          </div>
        </section>

        <section className="kanagawa">
          <h2>神奈川県内の従業員5名以下、<br className="sp"/>小さな会社の経営者様</h2>
          <div className="kanagawa-bg">
            <h3><span className="bg-yellow">デジタル化・IT活用で、<br className="sp"/>こんなお悩みありませんか？</span></h3>
            <div className="kanagawa-inner">
              <div className="kanagawa-frame">
                <StaticImage 
                  className="ph1" 
                  src="../images/main-home/kanagawa-ph1.png"
                  alt="デジタル化・IT活用で、こんなお悩みありませんか？"
                  placeholder="blurred"
                />
                <ul>
                  <li><span>そもそも、デジタル化やIT活用がよくわからない</span></li>
                  <li><span>気軽に相談できる人もいない</span></li>
                  <li><span>IT人材を新たに雇用する余裕もない</span></li>
                  <li><span>すでにIT導入しているが、上手く使いこなせていない</span></li>
                  <li><span>複数のクラウドサービスやアプリを使っているが、重複する機能もあって無駄な気がする</span></li>
                  <li><span>使う人によってやり方がバラバラで、かえって非効率</span></li>
                  <li><span>テレワークやオンライン会議を導入したいがセキュリティに不安がある</span></li>
                  <li><span>デジタル化を推進したいが情報セキュリティ教育に不安がある</span></li>
                  <li><span>IT導入・デジタル化したいが費用が不安</span></li>
                  <li><span>せっかくデジタル化するなら、確実に収益アップにつなげたい</span></li>
                  <li><span>小規模事業者持続化補助金でホームページを作りたい</span></li>
                </ul>
              </div>
            </div>
          </div>
        </section>
  
        <section className="kanagawa2">
          <div className="kanagawa2-inner">
            <h3><span className="bg-yellow">“小さな会社のためのデジタル戦略室”<br className="pc"/>株式会社ホットソウルが解決します！</span></h3>
            <StaticImage
              className="ph1"
              src="../images/main-home/kanagawa2-ph1.png"
              alt="“小さな会社のためのデジタル戦略室 ”株式会社ホットソウルが解決します！"
              placeholder="blurred"
            />
            <p className="txt1">
              <span className="bg-yellow">弊社は、IT業界歴20年以上のエンジニア経験と<br className="pc"/>中小企業診断士の知見を活かした、小規模事業者向け<br className="pc"/>ITコンサルティング事業を行っております。</span>
            </p>
            <p className="txt2">
              「ITに詳しい社員がいない。でも、新しくIT担当者を雇用する余裕もない」<br className="pc"/>
                そんな経営者の方のお悩みを解決します。<br/>“あなたの会社のデジタル戦略室”として、お気軽にご相談ください。 
            </p>
          </div>
        </section>
  
        <section className="serviceEX">
          <h2>サービス例</h2>
          <div className="serviceEX-inner">
            <div className="flex">
              <div className="flex-item item1">
                <p className="txt1"><span className="bg-yellow">ITツールの選定</span></p>
                <p className="txt2">御社に最適な<br className="pc"/>ITツールの選定をします。</p>
              </div>
              <div className="flex-item item2">
                <p className="txt1">
                  <span className="bg-yellow">デジタル化による<br/>業務効率化、<br/>生産性向上のサポート</span>
                </p>
                <p className="txt2">デジタル化へのサポートを通して、<br className="pc"/>業務の効率化を促します。</p>
              </div>
              <div className="flex-item item3">
                <p className="txt1">
                  <span className="bg-yellow">ITツール使用時の<br/>業務フローの整理、<br/>標準化</span>
                </p>
                <p className="txt2">社内のITツール使用時のルールを定め、<br className="pc"/>属人化を防ぎます。</p>
              </div>
              <div className="flex-item item4">
                <p className="txt1">
                  <span className="bg-yellow">テレワーク環境<br/>整備相談</span>
                </p>
                <p className="txt2">情報セキュリティ対策も含め、<br className="pc"/>テレワーク環境のサポートをします。</p>
              </div>
              <div className="flex-item item5">
                <p className="txt1">
                  <span className="bg-yellow">情報セキュリティ<br/>対策・教育</span>
                </p>
                <p className="txt2">情報漏洩リスクを削減し、<br className="pc"/>安心・安全なIT環境を構築します。</p>
              </div>
            </div>
            <div className="main-home-btn">
              <Link to={`/case-study/kanno-sharoshi`}>サポート事例・実績をみる</Link>
            </div>
          </div>
        </section>
  
        <section className="serviceFeatures">
          <h2>サービスの特長</h2>
          <div className="serviceFeatures-inner">
            <div className="box">
              <h3 className="icon1">
                <span className="bg-yellow">現役エンジニアが、<br/>「IT愛」と最新情報を基に“熱い思い”でご対応</span>
              </h3>
              <div className="bg">
                <p>
                  変化の激しいIT業界において、現場にいるという点はとても重要です。例えば、セキュリティ関連の脆弱性情報などは、現場を離れると最新情報を得にくくなります。その点、弊社担当者は20年以上の経験を持つ現場のシステムエンジニア。ソフトウェア製品やサービスに対する理解度も、単に知識として知っているだけでなく、より深く体験的に理解できています。それは例えるなら、車好きの人が、エンジン音・ロードノイズの伝わり方・ステアリングの感触・サスペンションの硬さ等々に敏感なことに似ています。<br/>
                  「知識としてよく知っている」＋「普段からよくいじっている」＋「好き」。
                </p>
              </div>
              <div className="flex">
                <div className="flex-item left">
                  <StaticImage 
                    src="../images/main-home/serviceFeatures-ph1.png"
                    alt="現役システムエンジニアが、「IT愛」と最新情報を基に“熱い思い”でご対応"
                    placeholder="blurred"
                  />
                </div>
                <div className="flex-item right">
                  <p>
                    だから、知識にかたよったITコンサルタントや、<br className="pc"/>経験の浅い元エンジニアやプログラマーからは得られない、<br className="pc"/>“効果と感動価値”をご提供します。
                  </p>
                </div>
              </div>
            </div>
            <div className="box">
              <h3 className="icon2">
                <span className="bg-yellow">中小企業診断士が、<br/>成長戦略に基づく<br className="sp"/>”冷静な視点”でアドバイス</span>
              </h3>
              <div className="bg">
                <p>
                  デジタル化やIT活用は、経営の目的ではなく手段です。事業の中にITを導入することがゴールではありません。「ITを含んだ形で経営を俯瞰的にみる」こと、その上で「結果を出す」ことこそ、本当の目的であると弊社は考えます。弊社担当者はシステムエンジニアであり、かつ、中小企業診断士の資格を持つ経営のプロでもあります。経営戦略を見据えた上でのIT活用で、御社に新時代の成長をもたらします。
                </p>
              </div>
            </div>
            <div className="box">
              <h3 className="icon3">
                <span className="bg-yellow">地元神奈川の小規模事業者を対象にした、<br className="pc"/>親身できめ細かなサービス</span>
              </h3>
              <div className="bg">
                <p>
                  地元神奈川で創業し10年以上、大好きな神奈川を盛り上げるため、小さな会社が元気になるお手伝いをさせてください！お金をかけなくても、社員が少なくても、工夫次第でデジタル化・IT導入は可能です。顔が見える距離感を大切に、１つ１つの疑問にお答えしながら、目標達成までしっかり伴走いたします。あなたの会社の“デジタル戦略担当”として、お気軽にご活用ください。
                </p>
              </div>
            </div>
          </div>
        </section>

        <section className="serviceFlow">
          <h2>サービスの流れ</h2>
          <div className="serviceFlow-inner">
            <h3>ご契約前</h3>
            <div className="flex flex-parent">
              <div className="flex-item left pc">
                <StaticImage
                  src="../images/main-home/serviceFlow-before-ph.png"
                  alt="ご契約前"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <div className="flex flex-child flex-child1">
                  <div className="flex-item left">
                    <p>オンライン<br className="pc"/>無料相談<br/><span>（30分程度）</span></p>
                  </div>
                  <div className="flex-item right">
                    <p>
                      サイトの入力フォームからお申し込みください。受付後、メールにてご返信し、日程調整いたします。Zoom等でお困りごとやご要望の概要をお伺いします。弊社でご対応できない案件については、この時点でお断りする場合がございます。
                    </p>
                    <div className="main-home-btn2">
                      <Link to={`/contact-us`}>オンライン無料相談はこちら</Link>
                    </div>
                  </div>
                </div>
                <div className="flex flex-child flex-child2">
                  <div className="flex-item left">
                    <p>訪問<br className="pc"/>無料相談<br/><span>（1時間程度）</span></p>
                  </div>
                  <div className="flex-item right">
                    <p>
                      対面にて詳細なヒアリングをさせていただきます。その場で助言できるものはできるだけお応えします。合わせて、弊社でお手伝いする場合のイメージをお伝えします。
                    </p>
                  </div>
                </div>
                <div className="flex flex-child flex-child3">
                  <div className="flex-item left">
                    <p>ご提案</p>
                  </div>
                  <div className="flex-item right">
                    <p>ヒアリング結果をもとに、お悩み解消に向けた支援内容を提案書におまとめします。その際、概算のお見積りもご提出いたします。</p>
                  </div>
                </div>
                <div className="flex flex-child flex-child4">
                  <div className="flex-item left">
                    <p>ご契約</p>
                  </div>
                  <div className="flex-item right">
                    <p>提案書をもとに、支援内容（お見積り対象の作業）について意識合わせを行います。お見積り書とご契約書を作成し、ご契約となります。 </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <hr/>
          <div className="serviceFlow-inner serviceFlow2">
            <h3>ご契約後</h3>
            <div className="flex flex-parent">
              <div className="flex-item left pc">
                <StaticImage
                  src="../images/main-home/serviceFlow-after-ph.png"
                  alt="ご契約後"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <div className="flex flex-child flex-child5">
                  <div className="flex-item left">
                    <p>現状分析</p>
                  </div>
                  <div className="flex-item right">
                    <p>
                      現在の業務フロー、情報の流れ、各担当者の作業内容などをヒアリング調査します。<br className="pc"/>また、ご使用中のシステム、ネットワーク、アプリ、クラウドサービスなどを使用状況含めて現地調査にまいります。情報セキュリティ、事業継続性、生産性向上等の視点でも分析いたします。
                    </p>
                  </div>
                </div>
                <div className="flex flex-child flex-child6">
                  <div className="flex-item left">
                    <p>課題の抽出</p>
                  </div>
                  <div className="flex-item right">
                    <p>
                      現状分析の結果を踏まえ、今後どのようにしていきたいのかを、経営戦略、事業戦略の観点からもあぶり出します。改善したい事、抜本的に変革したい事、実現したい事などを『課題』として優先度を付けて整理していきます。
                    </p>
                  </div>
                </div>
                <div className="flex flex-child flex-child7">
                  <div className="flex-item left">
                    <p>最適化</p>
                  </div>
                  <div className="flex-item right">
                    <p>優先度を付けた課題を達成するべく、施策の実行を支援します。基本的には、新しいものを導入するというより、今あるものを（あまりコストをかけずに）上手く使えるように改善していくことを目指します。業者との窓口に立ったり、軽作業（データの移行など）は弊社で実施いたします。</p>
                  </div>
                </div>
                <div className="flex flex-child flex-child8">
                  <div className="flex-item left">
                    <p>継続支援</p>
                  </div>
                  <div className="flex-item right">
                    <p>最適化後の運用をフォローします。<br/>細かい改善点にご対応いたします。</p>
                  </div>
                </div>
              </div>
            </div>
            <p className="att">※一般的な場合</p>
          </div>
        </section>
        <section>
          <Campaign/>
        </section>  
      </div>
    </Layout>
  )
}
